import React, { Component } from 'react';
import GoogleMap from 'google-map-react';

export class Map extends Component {
    static displayName = Map.name;

    constructor(props) {
        super(props);
    }

    renderMarkers(map, maps)
    {
        var contentString = `
            <div style="padding:10px 5px 10px 5px;">
                <p> 
                    <strong style="color: #4381ec; line-height:25px;">Sports Center</strong><br/>
                    Ben Gurion University, Beer-Sheva, Israel
                    <img src="data:image/png;base64," alt=":)" style="width:26px; padding-left:10px;" />
                </p>
            </div>`;

        let marker = new maps.Marker({
            position: { lat: 31.2612000, lng: 34.8067033 },
            map,
            title: 'Dojo (location)'
        });

        let infowindow = new maps.InfoWindow({
            content: contentString
        });

        infowindow.open(map, marker);
    }

    render()
    {

        return (
            <GoogleMap
                bootstrapURLKeys={{
                    key: "AIzaSyAY2VkeP0cYEjTTvwSGXpZU9k50VZsx3Ck",
                    language: "en",
                    region: "US"
                }}
                defaultCenter={{ lat: 31.2612000, lng: 34.8067033 }}
                defaultZoom={16}
                onGoogleApiLoaded={({ map, maps }) => this.renderMarkers(map, maps)}
            >
            </GoogleMap>
        );
    }
}


